/* Layout helpers
----------------------------------*/

.ui-helper-hidden {
  display: none;
}

.ui-helper-hidden-accessible {
  position: absolute !important;

  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

.ui-helper-reset {
  font-size: 100%;
  line-height: 1.3;

  margin: 0;
  padding: 0;

  list-style: none;

  text-decoration: none;

  border: 0;
  outline: 0;
}

.ui-helper-clearfix {
  display: inline-block;
  &:after {
    display: block;
    visibility: hidden;
    clear: both;

    height: 0;

    content: '.';
  }
}

/* required comment for clearfix to work in Opera \*/

* html .ui-helper-clearfix {
  height: 1%;
}

.ui-helper-clearfix {
  display: block;
}

/* end clearfix */

.ui-helper-zfix {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  opacity: 0;

  filter: Alpha(Opacity = 0);
}

/* Interaction Cues
----------------------------------*/

.ui-state-disabled {
  cursor: default !important;
}

/* Icons
----------------------------------*/

/* states and images */

.ui-icon {
  display: block;
  overflow: hidden;

  text-indent: -99999px;

  background-repeat: no-repeat;
}

